.movie {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3%;
    background-color: black;
}

.movie__intro {
    width: 100%;
}

.movie__backdrop {
    width: 100%;
    height: 400px;
    /* Adjusted for smaller screens */
    object-fit: cover;
    object-position: 0 35%;
}

.movie__detail {
    align-items: center;
    width: 90%;
    display: flex;
    flex-direction: column;
    /* Stacked layout for smaller screens */
    position: relative;
    bottom: 0;
    /* Removed overlap for smaller screens */
}

.movie__detailLeft {
    margin-bottom: 20px;
}

.movie__poster {
    width: 200px;
    /* Reduced size for smaller screens */
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
}

.movie__detailRight {
    color: white;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
}

.movie__detailRightTop>div {
    text-shadow: 0px 0px 5px #000000;
    margin-bottom: 0.5rem;
}

.movie__name {
    font-weight: 600;
    font-size: 2rem;
    /* Adjusted font size for smaller screens */
}

.movie__voteCount {
    margin-left: 0.5rem;
    font-size: 0.9rem;
}

.movie__genres {
    margin-bottom: 1rem;
    /* Add space below genres */
    padding-bottom: 1rem;
    /* Additional padding to ensure space is created */
}

.movie__genre {
    padding: 0.4rem;
    border: 2px solid white;
    border-radius: 20px;
    margin-right: 0.5rem;
    font-size: 0.9rem;
}

.movie__detailRightBottom {
    margin: 1rem 0;
    font-size: 1rem;
}

.movie_review {
    font-size: 1.2rem;
    /* Adjusted font size for smaller screens */
    margin-bottom: 1rem;
    font-weight: 600;
}

.movie__links {
    position: relative;
    display: flex;
    flex-direction: column;
    /* Stack links for smaller screens */
    align-items: center;
    margin-top: 1.5rem;
    width: 90%;
}

.movie__heading {
    font-size: 1.8rem;
    /* Adjusted font size for smaller screens */
    margin-bottom: 1rem;
    text-align: center;
}

.review__Button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.6rem 1.5rem;
    /* Reduced padding for smaller screens */
    border-radius: 20px;
    cursor: pointer;
    width: 120px;
    color: rgb(1, 1, 1);
    /* Ensure text is visible on dark background */
    font-weight: bold;
    font-size: 0.9rem;
    margin-bottom: 1rem;
    text-align: center;
    transition: background-color 0.3s ease;
    /* Smooth transition for hover effect */
}

.trailer__button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.6rem 1.5rem;
    /* Reduced padding for smaller screens */
    border-radius: 20px;
    cursor: pointer;
    width: 120px;
    color: rgb(5, 5, 5);
    /* Ensure text is visible on dark background */
    font-weight: bold;
    font-size: 0.9rem;
    margin-bottom: 1rem;
    text-align: center;
    transition: background-color 0.3s ease;
    /* Smooth transition for hover effect */
}

.youtube_review__button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.6rem 1.5rem;
    /* Reduced padding for smaller screens */
    border-radius: 20px;
    cursor: pointer;
    width: 120px;
    color: rgb(5, 5, 5);
    /* Ensure text is visible on dark background */
    font-weight: bold;
    font-size: 0.9rem;
    margin-bottom: 1rem;
    text-align: center;
    transition: background-color 0.3s ease;
    /* Smooth transition for hover effect */
}

.movie_trailerButton {
    background-color: #f3ce13;
    /* YouTube color */
}

.movie_trailerButton:hover {
    background-color: #d1a513;
    /* Darker shade on hover */
}

.movie__reviewButton {
    background-color: #f3ce13;
    /* A bold crimson red that creates contrast */
}

.movie__reviewButton:hover {
    background-color: #d1a513;
    /* Darker shade on hover */
}


.youtubereview_button {
    background-color: #f3ce13;
    /* A bold crimson red that creates contrast */
}

.youtubereview_button:hover {
    background-color: #d1a513;
    /* Darker shade on hover */
}

.movie__trailer {
    margin: 1.5rem 0;
    width: 90%;
    max-width: 560px;
    /* Adjust width for larger screens */
    aspect-ratio: 16 / 9;
    /* Maintain 16:9 aspect ratio */
    position: relative;
}

.movie__trailer iframe {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.streaming__info {
    margin-top: 20px;
    display: flex;
    align-items: center;
    gap: 0px;
}

.streaming__logo {
    height: 80px;
    width: 130px;
    align-items: center;
}

.streaming__info p {
    margin: 0;
    font-size: 24px;
    color: #f3ce13;
    /* Adjust based on your theme */
}

/* Ensure alignment on one line */
.movie__feedback {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* Adjust spacing between text and icons */
    gap: 10px;
    /* Small gap for spacing */
    flex-wrap: nowrap;
    /* Prevent wrapping */
    margin-top: 10px;
}

.feedback__text {
    white-space: nowrap;
    /* Prevent text from wrapping */
    font-size: 16px;
    color: #ffffff;
    margin: 0;
}

.movie__feedbackIcons {
    display: flex;
    gap: 15px;
    /* Space between thumbs-up and thumbs-down icons */
}

.movie__feedbackIcons i {
    font-size: 24px;
    cursor: pointer;
    transition: color 0.3s ease, transform 0.2s ease;
}

.movie__feedbackIcons i:hover {
    transform: scale(1.2);
}

.movie__feedbackIcons .active-feedback {
    color: #f3ce13;
    /* Green for thumbs-up */
}

.movie__feedbackIcons .fa-thumbs-down.active-feedback {
    color: #ff0000;
    /* Red for thumbs-down */
}

/* Movie Crew */

.meet-the-crew-heading {
    text-align: center;
    color: #f3ce13;
    /* This changes the text color to #f3ce13 */
}

.movie__crewListContainer {
    width: 690px;
    height: 300px;
    /* Set a fixed height to prevent vertical scroll */
    overflow-x: auto;
    /* Horizontal scroll */
    overflow-y: hidden;
    /* Prevent vertical scrolling */
    white-space: nowrap;
    padding: 10px 0;
}

.movie__crewList {
    display: flex;
    gap: 10px;
    /* Space between crew cards */
    padding: 0 10px;
}

.movie__crewMember {
    flex: 0 0 auto;
    /* Prevent stretching */
    width: 140px;
    /* Fixed width for each card */
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    transition: transform 0.3s ease;
}

.movie__crewMember:hover {
    transform: scale(1.05);
}

.movie__crewImage {
    width: 150px;
    height: 130px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 10px;
    border: 3px solid #f3ce13;
}

.movie__crewName {
    font-weight: bold;
    color: #f3ce13;
    margin-bottom: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.movie__crewJob {
    font-size: 0.8rem;
    color: #ccc;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* Scrollbar styling */
.movie__crewListContainer::-webkit-scrollbar {
    height: 0px;
}

.movie__crewListContainer::-webkit-scrollbar-track {
    background: #333;
}

.movie__crewListContainer::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 4px;
}

.movie__crewListContainer::-webkit-scrollbar-thumb:hover {
    background: #777;
}




/* Responsive design for smaller screens */
@media (max-width: 600px) {
    .feedback__text {
        font-size: 14px;
        /* Reduce font size for text */
    }

    .movie__feedbackIcons i {
        font-size: 20px;
        /* Reduce icon size */
    }

    .movie__crewMember {
        width: 90px;
    }

    .movie__crewImage {
        width: 100px;
        height: 100px;
    }

    .movie__crewListContainer {
        width: 390px;
        height: 200px;
        /* Set a fixed height to prevent vertical scroll */
        overflow-x: auto;
        /* Horizontal scroll */
        overflow-y: hidden;
        /* Prevent vertical scrolling */
        white-space: nowrap;
        padding: 10px 0;
    }
    .movie__backdrop {
        width: 400px;
        height: 350px;
        /* Adjusted for smaller screens */
        object-fit: cover;
        object-position: 10% 90%;
    }

}



@media (min-width: 768px) {

    .movie__links {
        flex-direction: row;
        /* Side-by-side layout for larger screens */
        justify-content: space-between;
        width: 75%;
        /* Use 75% width for a balanced layout */
    }

    /* Adjust the crew list container */
    .movie__crewListContainer {
        width: 1250px;
        height: 220px;
        /* Set a fixed height to prevent vertical scroll */
        overflow-x: auto;
        /* Horizontal scroll */
        overflow-y: hidden;
        /* Prevent vertical scrolling */
        white-space: nowrap;
        padding: 10px 0;
    }

    /* Scrollbar styling */
    .movie__crewListContainer::-webkit-scrollbar {
        height: 0px;
    }

    /* Adjust crew member cards */
    .movie__crewMember {
        width: 140px;
        /* Increase width for a better appearance on tablets */
    }

    .movie__crewImage {
        width: 110px;
        /* Increase the image size */
        height: 110px;
    }

    /* Adjust font sizes for better readability */
    .feedback__text {
        font-size: 16px;
        /* Slightly larger font size */
    }

    .movie__feedbackIcons i {
        font-size: 24px;
        /* Slightly larger icon size */
    }
}